import React, { useState } from 'react'
import { Box, TextInput, Button, TextArea } from 'grommet'
import { FormNextLink } from 'grommet-icons'
import BackButton from './global/buttons/BackButton'
import { White } from './global/colors'

function PostJobDescription() {
  const [description, setDescription] = useState('')

  const [responsibility1, setResponsibility1] = useState('')
  const [responsibility2, setResponsibility2] = useState('')
  const [responsibility3, setResponsibility3] = useState('')
  const [responsibility4, setResponsibility4] = useState('')
  const [responsibility5, setResponsibility5] = useState('')

  const [experience1, setExperience1] = useState('')
  const [experience2, setExperience2] = useState('')
  const [experience3, setExperience3] = useState('')
  const [experience4, setExperience4] = useState('')
  const [experience5, setExperience5] = useState('')

  return (
    <div>
      <h3>Description</h3>
      <Box width='xlarge'>
        <TextArea
          placeholder='Describe a little about the project your company/client is going through. Be concise. You only have 80 words!'
          value={description}
          onChange={event => setDescription(event.target.value)}
          resize={false} 
        />
      </Box>
      <h3>Job Responsibilities</h3>
      You can have up to five
      <Box width='medium'>
        <TextInput value={responsibility1} onChange={event => setResponsibility1(event.target.value)} placeholder='Type job responsibility here' />
        <TextInput value={responsibility2} onChange={event => setResponsibility2(event.target.value)} placeholder='Type job responsibility here' />
        <TextInput value={responsibility3} onChange={event => setResponsibility3(event.target.value)} placeholder='Type job responsibility here' />
        <TextInput value={responsibility4} onChange={event => setResponsibility4(event.target.value)} placeholder='Type job responsibility here' />
        <TextInput value={responsibility5} onChange={event => setResponsibility5(event.target.value)} placeholder='Type job responsibility here' />
      </Box>
      <h3>Required Experience</h3>
      You can have up to five
      <Box width='medium'>
        <TextInput value={experience1} onChange={event => setExperience1(event.target.value)} placeholder='Type previous experience here' />
        <TextInput value={experience2} onChange={event => setExperience2(event.target.value)} placeholder='Type previous experience here' />
        <TextInput value={experience3} onChange={event => setExperience3(event.target.value)} placeholder='Type previous experience here' />
        <TextInput value={experience4} onChange={event => setExperience4(event.target.value)} placeholder='Type previous experience here' />
        <TextInput value={experience5} onChange={event => setExperience5(event.target.value)} placeholder='Type previous experience here' />
      </Box>
      <Box direction='row' justify='end'>
        <Box width='small'>
          <BackButton onClick={() => { }} />
        </Box>
        <Box width='small'>
          <Button primary label='Continue' icon={<FormNextLink color={White} />} reverse={true} onClick={() => { }} />
        </Box>
      </Box>
    </div>
  )
}

export default PostJobDescription;