import React, { useState } from 'react'
import { Box, TextInput, Button, RadioButton, Select } from 'grommet'
import { FormNextLink } from 'grommet-icons'
import BackButton from './global/buttons/BackButton'
import { White } from './global/colors'

function PostSalaryBenefits() {
  const [position, setPosition] = useState('');
  const onPositionChange = event => setPosition(event.target.value);

  const [skill, setSkill] = useState('');
  const onSkillChange = event => setSkill(event.target.value);

  const [selected, setSelected] = useState();

  const [currency, setCurrency] = useState('');
  const [minSalary, setMinSalary] = useState('');
  const [maxSalary, setMaxSalary] = useState('');
  const [bonus, setBonus] = useState('');
  return (
    <div>
      <h3>Position</h3>
      <Box width='medium'>
        <TextInput value={position} onChange={onPositionChange} placeholder='Java developer' />
      </Box>
      <h3>Skills</h3>
      <Box width='medium'>
        <TextInput value={skill} onChange={onSkillChange} placeholder='Type a skill' />
      </Box>
        Smart suggestions based n other positions like yours<br />
          tags go here
      <h3>Employment Type</h3>
      <Box align='center' direction='row'>
        <Box margin='small'>
          <RadioButton
            label='Permanent'
            name='name'
            value='permanent'
            checked={selected === 'permanent'}
            onChange={event => setSelected(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Freelance'
            name='name'
            value='freelance'
            checked={selected === 'freelance'}
            onChange={event => setSelected(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Part-time'
            name='name'
            value='parttime'
            checked={selected === 'parttime'}
            onChange={event => setSelected(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Internship'
            name='name'
            value='internship'
            checked={selected === 'internship'}
            onChange={event => setSelected(event.target.value)}
          />
        </Box>
      </Box>
      <h3>Salary</h3>
      <Box width='small'>
        <Select
          options={['GBP', 'EUR', 'USD']}
          value={currency}
          onChange={({ option }) => setCurrency(option)}
          placeholder='Currency'
        />
      </Box>
      <Box align='center' direction='row'>
        {/* TODO: Make MaskedInputs */}
        <TextInput value={minSalary} onChange={event => setMinSalary(event.target.value)} placeholder='50,000' />
        <TextInput value={maxSalary} onChange={event => setMaxSalary(event.target.value)} placeholder='60,000' />
      </Box>
      <h3>Bonus</h3>
      <Box width='medium'>
        <TextInput value={bonus} onChange={event => setBonus(event.target.value)} placeholder='Personal Performance, Stocks...' />
      </Box>
      <Box direction='row' justify='end'>
        <Box width='small'>
          <BackButton onClick={() => { }} />
        </Box>
        <Box width='small'>
          <Button primary label='Continue' icon={<FormNextLink color={White} />} reverse={true} onClick={() => { }} />
        </Box>
      </Box>
    </div>
  );
}

export default PostSalaryBenefits;